export function loadKeyrus() {
    console.log('cargando modulos js')
    const body = document.querySelector("body"),
        sidebar = body.querySelector(".ksidebar"),
        toggle = body.querySelector(".toggle"),
        navbar = body.querySelector(".knavbar"),
        menulinks = body.querySelectorAll(".menu-links"),
        // searchBtn = body.querySelector(".search-box"),
        modeSwitch = body.querySelector(".toggle-switch"),
        modeText = body.querySelector(".mode-text");




    toggle.addEventListener("click", () => {
        sidebar.classList.toggle('close')
        navbar.classList.toggle('close')
    })
    modeSwitch.addEventListener("click", () => {
        body.classList.toggle("dark");

        if(body.classList.contains("dark")){
            modeText.innerText = "Light Mode"
        }else{
            modeText.innerText = "Dark Mode"
        }

    })
    menulinks.forEach(menulink => {
        // console.log(menulink)
        let hasmenu = menulink.querySelector(".has-submenu")
        if(hasmenu)
        {
            hasmenu.addEventListener("click",()=>{
                hasmenu.classList.toggle("active")
                let submenu = menulink.querySelector(".submenu")
                if(submenu)
                {
                    submenu.classList.toggle("open")
                    console.log(submenu)
                }
            })
        }
        console.log(hasmenu)

        // let active = menulink.querySelector(".active")
        // if(active)
        // {
        //     let submenu = menulink.querySelector(".submenu")
        //     if(submenu)
        //     {

        //     }
        // }
        // console.log(menu)
        // if(menu.contains("nav-link"))
        // {
        //     console.log(menulink)
        // }
    });
    // console.log(menulinks)
    // console.log(body)
    // console.log(sidebar)
}
