import AuthService from '../services/auth_service.ds'

 
const user =  JSON.parse(localStorage.getItem('user') ?? '{}')
// console.log(user)
const account =  JSON.parse(localStorage.getItem('account') ?? '{}');
// console.log(rol)
// const institution = JSON.parse(typeof localStorage.getItem('institution'));
const initialState = user
  ? { status: { loggedIn: true }, user , account}
  : { status: { loggedIn: false }, user: null , account: null};
export const auth = {
  namespaced: true,
  state: initialState,
  actions: {
    login({ commit }: any , user: any) {
      return AuthService.login(user).then(
        user => {
          commit('loginSuccess', user);
          return Promise.resolve(user);
        },
        error => {
          commit('loginFailure');
          return Promise.reject(error);
        }
      );
    },
    logout({ commit }: any) {
      AuthService.logout();
      commit('logout');
    },
    // changeRol({commit},rol){
    //   AuthService.changeRol(rol);
    //   commit('changeRol',rol)
    // },
    // changeInstitution({commit},institution){
    //   AuthService.changeInstitution(institution);
    //   commit('changeInstitution',institution)
    // }
    // register({ commit }, user) {
    //   return AuthService.register(user).then(
    //     response => {
    //       commit('registerSuccess');
    //       return Promise.resolve(response.data);
    //     },
    //     error => {
    //       commit('registerFailure');
    //       return Promise.reject(error);
    //     }
    //   );
    // }
  },
  mutations: {
    loginSuccess(state: any, user: any) {
      state.status.loggedIn = true;
      state.user = user;
    },
    loginFailure(state: any) {
      state.status.loggedIn = false;
      state.user = null;
    },
    // logout(state) {
    //   state.status.loggedIn = false;
    //   state.user = null;
    // },
    // changeRol(state,rol)
    // {
    //   state.rol = rol
    // },
    // changeInstitution(state,institution)
    // {
    //   state.institution = institution
    // }
    // registerSuccess(state) {
    //   state.status.loggedIn = false;
    // },
    // registerFailure(state) {
    //   state.status.loggedIn = false;
    // }
  }
};
