import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/Authentication/LoginView.vue'
const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta:{
      requireAuth: true
    }
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView,
    meta:{
      requireAuth: false
    }
  },
  {
    path: '/type_person',
    name: 'type_person',
    component: () => import( '../views/TypePerson/ListView.vue'),
    meta:{
      requireAuth: true
    }
  },
  {
    path: '/city',
    name: 'city',
    component: () => import( '../views/City/ListView.vue'),
    meta:{
      requireAuth: true
    }
  },
  {
    path: '/country',
    name: 'country',
    component: () => import( '../views/Country/ListView.vue'),
    meta:{
      requireAuth: true
    }
  },
  {
    path: '/clients',
    name: 'clients',
    component: () => import( '../views/clients/ListView.vue'),
    meta:{
      requireAuth: true
    }
  },
 
  {
    path: '/clients/create',
    name: 'clients_create',
    component: () => import( '../views/clients/CreateView.vue'),
    meta:{
      requireAuth: true
    }
  },

  {
    path: '/clients/show/:id',
    name: 'clients_show',
    component: () => import( '../views/clients/ShowView.vue'),
    meta:{
      requireAuth: true
    }
  },

  {
    path: '/clients/edit/:id',
    name: 'clients_edit',
    component: () => import( '../views/clients/EditView.vue'),
    meta:{
      requireAuth: true
    }
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
